import 'rxjs/add/operator/toPromise';

import { Injectable } from '@angular/core';

import { Api } from '../api/api';
import { Account_ } from '../../interface/account';
import Swal from 'sweetalert2'
import { LoadingController } from 'ionic-angular';
/**
 * Most apps have the concept of a User. This is a simple provider
 * with stubs for login/signup/etc.
 *
 * This User provider makes calls to our API at the `login` and `signup` endpoints.
 *
 * By default, it expects `login` and `signup` to return a JSON object of the shape:
 *
 * ```json
 * {
 *   status: 'success',
 *   user: {
 *     // User fields your app needs, like "id", "name", "email", etc.
 *   }
 * }
 * ```
 *
 * If the `status` field is not `success`, then an error is detected and returned.
 */
@Injectable()
export class User {
  _user: any;

  constructor(public api: Api, private loadingCtrl: LoadingController) { }

  login(accountInfo: Account_) {
    let loading = this.loadingCtrl.create({
      content: 'Por favor espere...'
    })
    loading.present();
    let seq = this.api.post('user/login', accountInfo).share();
    seq.subscribe((res: any) => {
        this._loggedIn(res);
      loading.dismiss();
    }, err => {
      Swal.fire({
        type: 'error',
        title: 'Oops...',
        text: 'Correo electrónico o contrasena incorrecta',
        background: '#transparent'
      })
      loading.dismiss();
    });

    return seq;
  }

  /**
   * Send a POST request to our signup endpoint with the data
   * the user entered on the form.
   */
  signup(accountInfo: any) {
    let seq = this.api.post('signup', accountInfo).share();

    seq.subscribe((res: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (res.status == 'success') {
        this._loggedIn(res);
      }
    }, err => {
      console.error('ERROR', err);
    });

    return seq;
  }

  getUser() {
    let seq = this.api.get('user/perfil', null, true).share();
    seq.subscribe((res: any) => {
      console.log(res);
      
    }, err => {
      console.error('ERROR', err);
    });

    return seq;
  }
  /**
   * Log the user out, which forgets the session
   */
  logout() {
    this._user = null;
  }

  _loggedIn(resp) {
    this._user = resp.user;
  }
}



